import React, { useEffect, useState, useCallback } from "react";
import { useAuth } from "../../../../context/auth";
import API from "../../../../Api";

import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

const ShipNameSelect = (props) => {
  const token = useAuth().authTokens;
  const [ship, setShip] = useState("");
  const [shipOptions, setShipOptions] = useState([]);

  const loadShips = useCallback(() => {
    API.get("carrier/transport/ships", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let ships = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempShipArr = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          ships.push(tempShipArr);
        }
      }

      setShipOptions(ships);
    });
  }, []);

  const setShipSelected = (value) => {
    props.setShipName(value);
    setShip(value);

    if (props.getShipDetails) {
      props.getShipDetails(value);
    }
  };

  useEffect(() => {
    loadShips();
    return () => {};
  }, [loadShips]);

  const handleShipSelection = (value) => {
    props.setShipName(value);
    setShipSelected(value);
  };

  return (
    <div>
      <TextField
        select
        label="Select a Ship"
        name="shipName"
        value={props.shipName || ship}
        onChange={(e) => handleShipSelection(e.target.value)}
        fullWidth
        required
        disabled={props.disabled || false}
      >
        {shipOptions.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </div>
  );
};

export default ShipNameSelect;
