import React, { useState } from 'react'
import API from '../../../Api'
import Swal from 'sweetalert2'
import DateFnsUtils from '@date-io/date-fns'

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Input,
    Col
} from 'reactstrap'
import TextField from '@material-ui/core/TextField'
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from '@material-ui/pickers'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFileImport, faWindowClose, faCalendarDay, faAnchor, faFileExcel, faFileDownload } from '@fortawesome/free-solid-svg-icons'

import FileTemplate from '../../../assets/template/Template.xlsx'

import { useAuth } from "../../../context/auth"

import ShipNameSelect from "../Forms/Fields/ShipNameSelect"
import LinearProgressBarWithLabel from '../Common/LinearProgressBarWithLabel'

const NewPassengerManifests = (props) => {
    const authToken = useAuth().authTokens.access_token
    const [isProcessing, setIsProcessing] = useState(false)
    const [excelFile, setExcelFile] = useState(null)
    const [departureDate, setDepartureDateChange] = useState(null)
    const [arrivalDate, setArrivalDateChange] = useState(null)
    const [transportNumber, setTransportNumber] = useState("")
    const [shipName, setShipName] = useState("")
    const [transportType, setTransportType] = useState(2)
    const [voyageRoute, setVoyageRoute] = useState("")

    const dFormat = new DateFnsUtils()

    const getShipDetails = (shipId) => {
        API.get(`carrier/transport/ship/${shipId}`, {
          headers: {
            Authorization: "bearer " + authToken,
          },
        }).then((result) => {
            let data = result.data
            if(data != null){
                setVoyageRoute(data.routes)
                setTransportType(data.transportation_category_id)
            }
        });
      };

    const importFileHandler = (e) => {

        e.preventDefault()
        setIsProcessing(true)
        let token = authToken
        let formData = new FormData()

        formData.append("profileFile", excelFile)
        formData.append("departure", dFormat.format(departureDate, "Y-M-dd H:m:s"))
        formData.append("arrival", dFormat.format(arrivalDate, "Y-M-dd H:m:s"))
        formData.append("transport_no", transportNumber)
        formData.append("ship_name", shipName)
        formData.append("transport_type", transportType)
        formData.append("route", voyageRoute)

        API.post('store/import/profiles', formData, {
            headers: {
                'Authorization': 'bearer ' + token,
                'Content-Type': 'multipart/form-data'
            }
        }).then(result => {
            if (result.data.status) {
                setIsProcessing(false)
                setExcelFile(null)
                setDepartureDateChange(null)
                setArrivalDateChange(null)
                setTransportNumber("")
                setTransportType("")
                setVoyageRoute("")

                Swal.fire(
                    'Transaction Completed!',
                    'Data Successfully Imported!',
                    'success'
                )
                
                props.loadPassenger()
                props.toggle()
            } else {
                setIsProcessing(false)
                Swal.fire(
                    'Transaction Failed!',
                    result.data.message,
                    'error'
                )
            }
        }).catch(e => {
            setIsProcessing(false)

            Swal.fire(
                'Transaction Failed!',
                'Failed to Import Data!',
                'error'
            )

        })
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-lg" style={{ marginTop: "7%", marginLeft: "30%"}}>
                <ModalHeader style={{ lineHeight: "2" }}>
                    Import Passenger Manifest <br />                    
                </ModalHeader>
                <form onSubmit={(e) => importFileHandler(e)} encType="multipart/form-data" id="excelFile">
                    <ModalBody>                        
                        <Row className="p-2">
                            <div className="col-md-6 p-2">
                                <h5 className="text-primary ml-2"><FontAwesomeIcon icon={faCalendarDay} /> Travel Dates</h5>
                                <Card className="border-0">
                                    <CardBody className="p-2">
                                        <div>
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <KeyboardDateTimePicker
                                                    value={departureDate}
                                                    onChange={setDepartureDateChange}
                                                    label="ETD(Est. Time of Departure)"
                                                    format="MMM/d/yyyy h:mm:ss a"
                                                    required
                                                    disablePast
                                                    clearable
                                                    hideTabs
                                                    showTodayButton
                                                    fullWidth
                                                />
                                            </MuiPickersUtilsProvider>
                                        </div>
                                        <div className="mt-2">
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <KeyboardDateTimePicker
                                                    value={arrivalDate}
                                                    onChange={setArrivalDateChange}
                                                    label="ETA(Est. Time of Arrival)"
                                                    format="MMM/d/yyyy h:mm:ss a"
                                                    required
                                                    disablePast
                                                    clearable
                                                    hideTabs
                                                    showTodayButton
                                                    fullWidth
                                                />
                                            </MuiPickersUtilsProvider>
                                        </div>
                                    </CardBody>
                                </Card>
                            </div>
                            <div className="col-md-6 p-2">
                                <h5 className="text-primary"><FontAwesomeIcon icon={faAnchor} /> Transport Details</h5>
                                <Card className="border-0">
                                    <CardBody className="p-1 m-1">
                                        <div>
                                            <ShipNameSelect getShipDetails={getShipDetails} setShipName={setShipName} />
                                        </div>
                                        <div className="mt-2 mb-2">
                                            <TextField
                                                value={transportNumber}
                                                onChange={(e) => setTransportNumber(e.target.value)}
                                                label="Voyage Number"
                                                fullWidth
                                                required
                                            />
                                        </div>                                        
                                        <div>
                                            <TextField
                                                label="Voyage Route"
                                                value={voyageRoute}
                                                onChange={(e) => setVoyageRoute(e.target.value)}
                                                fullWidth
                                                required
                                                disabled={true}
                                            />
                                        </div>
                                    </CardBody>
                                </Card>
                            </div>
                            <div className="col-md-12 pt-2">
                                <h5 className="text-success">
                                    <FontAwesomeIcon icon={faFileExcel} /> Import Excel File: <a href={FileTemplate} title="Download Passenger Manifest Template" download="Passenger-Manifest-Template.xlsx">
                                        <span style={{ fontSize: '0.9rem' }}><FontAwesomeIcon icon={faFileDownload} /> Download this template</span>
                                    </a><br />
                                </h5>
                                <Card>
                                    <CardBody className="p-1 m-0">
                                        <div className="m-0">
                                            <Input
                                                accept=".xlsx,.xls"
                                                id="raised-button-file"
                                                type="file"
                                                onChange={(e) => setExcelFile(e.target.files[0])}
                                                disabled={isProcessing}
                                                required
                                            />
                                        </div>
                                    </CardBody>
                                </Card>
                            </div>
                        </Row>
                        <div>
                            {
                                isProcessing &&
                                <Row className="p-2">
                                    <Col className="text-center">
                                        <LinearProgressBarWithLabel title="Importing Passenger Manifest" />
                                    </Col>
                                </Row>
                            }
                        </div>
                    </ModalBody>
                    <ModalFooter>                        
                        <Button type="submit" color="primary" disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faFileImport} /> Import
                        </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Cancel
                    </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    )
}

export default NewPassengerManifests 