import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "../../../../context/auth";
import API from "../../../../Api";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

const TransportType = (props) => {
  const token = useAuth().authTokens;
  const [transportType, setTransportType] = useState(props.transportType || "");
  const [transportOptions, setTransportOptions] = useState([]);

  const loadTransportType = useCallback(() => {
    API.get("carrier/transport/category", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let category = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempCategory = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          category.push(tempCategory);
        }
      }

      setTransportOptions(category);
    });
  }, []);

  useEffect(() => {
    loadTransportType();
    return () => {};
  }, [loadTransportType, token]);

  const handleChange = (value) => {
    setTransportType(value);
    props.setTransportType(value);
  };

  return (
    <div>
      <TextField
        select
        label="Select Transport Type"
        name="transportType"
        value={props.transportType || transportType}
        onChange={(e) => handleChange(e.target.value)}
        fullWidth
        required
      >
        {transportOptions.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </div>
  );
};

export default TransportType;
